.gallery {
  @include flex(row, center, center);
  flex-wrap: wrap;
  background-color: $color-2;
  gap: 50px;
  margin: 0 100px 50px 100px;
  padding: 50px;
  border-radius: 25px;
  height: 100%;
  @media (max-width: 500px) {
    @include flex(column, flex-start, flex-start);
    margin: 20px;
    gap: 20px;
    padding: 0;
    background: $color-3;
  }
  & img {
    height: 100%;
    width: 100%;
    border-radius: 10px;
  }
  &__cards {
    @include flex(row, center, normal);
    position: relative;
    height: 340px;
    width: 30%;
    cursor: pointer;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
      background: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(0, 0, 0, 0.5) 100%
      );
    }
    @media (max-width: 500px) {
      width: 100%;
      height: 255px;
    }
  }
}
