// Mixins

@mixin font-size-weight($size) {
  font-size: $size;
  font-weight: 500;
}

@mixin flex($direction, $justify, $align) {
  display: flex;
  flex-direction: $direction;
  justify-content: $justify;
  align-items: $align;
}

@mixin br-none($display) {
  .br-none {
    display: $display;
  }
}
