.header {
  @include flex(row, space-between, center);
  margin: 50px 0;
  @media (max-width: 500px) {
    position: relative;
    margin: 20px -20px 30px 20px;
  }
  &__image {
    margin-left: 100px;
    @media (max-width: 500px) {
      margin: 0;
      & img {
        width: 145px;
        height: 47px;
      }
    }
  }
  &__links {
    @include flex(row, flex-start, center);
    margin-right: 100px;
    gap: 57px;
    @media (max-width: 500px) {
      position: absolute;
      top: 20px;
      right: 40px;
      align-items: center;
      margin-right: 0;
      gap: 26px;
    }
    & a {
      text-decoration: none;
      color: $color-1;
      @include font-size-weight(24px);
      @media (max-width: 500px) {
        @include font-size-weight(12px);
      }
    }
  }
}

.nav-active {
  position: relative;
  &::after {
    content: "";
    height: 2px;
    width: 0;
    background: $color-1;
    position: absolute;
    border-radius: 10px;
    bottom: 0px;
    left: 50%;
    transform: translateX(-50%);
    -webkit-animation: anim 0.3s ease forwards;
    animation: anim 0.6s ease forwards;

    @keyframes anim {
      to {
        width: 100%;
      }
    }
  }
}
